ransition:all 300ms ease-in-out}.popup .dismiss-btn button:hover{color:#111;background:#f5f5f5}.popup div{position:relative;top:10px;opacity:0}.popup.active div{top:0px;opacity:1}.popup.active .icon{transition:all 300ms ease-in-out 250ms}.popup.active .title{transition:all 300ms ease-in-out 300ms}.popup.active .description{transition:all 300ms ease-in-out 350ms}.popup.active .dismiss-btn{transition:all 300ms ease-in-out 400ms}.navbar-chat{background:linear-gradient(45deg, #49cdd0, #00bcd4) !important}html{scroll-behavior:smooth !important}.bg-transparent{background:transparent !important}.main-default{display:flex;width:100%;height:100vh;flex-direction:column}.main-default .sidebar{margin-left:-300px;top:60px;height:calc( 100vh - 60px )}.main-default .sidebar.active{margin-left:0px !important}.main-default .sidebar .user-info .image img{height:80px}.main-default .sidebar .life{position:absolute;left:0;bottom:0;text-align:center;width:100%;padding:10px;color:#6f6fff;font-size:.9rem}.main-default__top{height:60px;width:100%;min-height:60px}.main-default__bottom{flex-grow:1;width:100%}.main-default__bottom .navbar{position:fixed;width:100vw;padding-right:20px !important}.main-default__bottom .content{margin:0 !important;padding:0px !important;display:flex;flex-direction:column}.main-default__bottom .content__bottom{flex-grow:1}.people-list{height:calc( 100vh - 170px );overflow-y:scroll}.chat-history{height:calc( 100vh - 370px );overflow-y:scroll;scroll-behavior:smooth}.user-item{transition:all .3s ease-in-out}.user-item:hover{transition:all .3s ease-in-out;background-color:#87e3ec !important}.user-item:hover img{-webkit-transform:scale(1.1);transform:scale(1.1)}.user-item:hover .name,.user-item:hover .status{font-weight:bold !important}.user-item .about .name{color:#000 !important}.user-item img.active{border:2px solid #0c9c0c8c !important}.user-item .status.active{color:#000 !important}.user-item .status i{color:#ccc}.user-item .status i.active{color:#0c9c0c8c}.chat-history{position:relative}.chat-history ul li{margin-bottom:20px}.chat-history .message-data{margin-bottom:0px !important}.chat-history .message{padding:8px 12px !important;border-radius:5px !important;max-width:70% !important;margin-bottom:0px !important}.chat-history .gif-loading{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.message-info{display:flex}.message-info__image{width:30px;height:30px;min-width:30px !important}.message-info__image img{border-radius:50%;object-fit:cover}.message-info__message{flex-grow:1;margin-left:5px}.message-info__message.right{display:flex;flex-direction:column;align-items:flex-end}.message-info__message .time span{font-size:12px;margin-left:2px}.color--red{color:red !important}.border--red{border-color:red !important}.change_passowrd .form-control:focus{border:1px solid #0020ff !important}.swal-footer{text-align:center !important}.left-0{left:0 !important}#leftsidebar li.active{background:#ccc2c269;border-radius:2px}@media screen and (max-width: 480px){.chat_detail .block-header .div_breadcrumb,.change_passowrd .block-header .div_breadcrumb{display:none !important}.chat_detail .chat-message,.change_passowrd .chat-message{padding:0px 40px 20px 40px !important}.chat_detail .chat-message .input-group,.change_passowrd .chat-message .input-group{padding-top:0px !important}.chat-history{height:auto !important}.chat-history ul li:first-child{display:block !important}.chat-history .message{max-width:85% !important;line-height:15px !important;font-size:15px !important}.change_passowrd h1{font-size:2rem;margin-bottom:0px !important}.chat-app .list_btn{bottom:15px !important;right:15px !important}.chat-app .people-list{position:fixed !important;left:-400px;top:55px !important;height:calc(100vh - 55px) !important;margin:0px !important;width:300px !important;box-shadow:5px 0px 16px #ccc !important}.slideDown{background-color:#fff !important;box-shadow:0px 0px 10px #ccc !important;min-he